.App{text-align:center;background-color:#0e101c}

.Header{font-family:'Reggae One', cursive;color:#f7f7f7}.Header__xlg{font-size:100rem}

.Button .btn{padding:9px;font-family:"Helvetica Neue", Helvetica, Arial, serif;font-weight:bolder;border-radius:4%}.Button .check_guess,.Button .play_again{background:#27ae60}.Button .check_guess:hover:enabled,.Button .play_again:hover:enabled{background:#2ecc71}.Button .check_guess:active,.Button .play_again:active{transition:0.3s all;transform:translateY(3px);border:1px solid transparent;opacity:0.8}.Button .check_guess:disabled,.Button .play_again:disabled{opacity:0.4;cursor:not-allowed}.start_new_game{background:#bdc3c7}.start_new_game:hover{background:#ecf0f1}.start_new_game:active{transition:0.3s all;transform:translateY(3px);border:1px solid transparent;opacity:0.8}

.Form{max-width:500px;margin:0 auto;display:flex;flex-direction:column;background:#0e101c}.Input.single__character{justify-content:space-between;width:45px;height:45px;margin:5px;border-radius:5px;text-align:center;font-size:34px;font-family:-apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial sans-serif}.Input.submit{padding:9px;-webkit-appearance:none}.Input.submit:hover{background:#bf1650}.Input.submit:active{transition:0.3s all;transform:translateY(3px);border:1px solid transparent;opacity:0.8}.Input.submit:disabled{opacity:0.4}.Input.form{display:block;box-sizing:border-box;width:100%;border-radius:4px;border:1px solid white;padding:10px 15px;margin-bottom:10px;font-size:14px}.error__input{color:#bf1650}.error__input:before{display:inline;content:"⚠ "}label{line-height:2;text-align:left;display:block;margin-bottom:4px;margin-top:7px;color:white;font-size:14px;font-weight:200}

.maximum_score{padding-bottom:18rem}.maximum_score h2{font-size:22rem;color:white}

.Game{display:flex;flex-direction:column;justify-content:space-around;height:50%}.Game__guess-word{display:flex;align-items:baseline;justify-content:center}.Game .letter{font-size:88rem;color:#f7f7f7}.Footer{display:grid;grid-row-gap:13rem;row-gap:13rem}.Footer .text{color:white;font-weight:bold;font-size:35rem}.Footer .Counter_timer{padding-top:33rem;margin:auto}

.GameOver{background:#0e101c}

table{background-color:#f7f7f7;border-spacing:0;border:1px solid black;margin:auto}table tbody tr:nth-of-type(odd){background-color:rgba(0,0,0,0.05)}table tr :last-child td{border-bottom:0}table th,table td{margin:0;padding:12.5rem;border-bottom:1px solid black;border-right:1px solid black}table th :last-child,table td :last-child{border-right:0}

.ScoreTable{display:grid;grid-row-gap:50rem}

